/* Контейнер для стеклянного меню */
.glassTabBarContainer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  padding-bottom: env(safe-area-inset-bottom, 0);
}

/* Градиентный фон под меню */
.glassTabBarGradient {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 120px;
  background: linear-gradient(
    to top,
    rgba(253, 242, 248, 0.95) 0%,
    rgba(248, 237, 253, 0.85) 30%,
    rgba(253, 242, 248, 0.4) 60%,
    rgba(253, 242, 248, 0) 100%
  );
  pointer-events: none;
}

/* ================================================================================================
 * ⛔️ КРИТИЧЕСКИ ВАЖНО: НЕ ИЗМЕНЯТЬ ПАРАМЕТРЫ ЭТОГО БЛОКА! ⛔️
 * ================================================================================================
 *
 * ❌ ЗАПРЕЩЕНО изменять следующие параметры без явного согласования:
 *    • margin: 12px 32px - отступы от краев экрана (32px слева и справа)
 *    • border-radius: 40px - сильное закругление углов в стиле iOS
 *    • padding: 6px - компактная высота панели
 *    • background: rgba(253, 242, 248, 0.12) - оптимальная прозрачность (видно объекты сзади)
 *    • backdrop-filter: blur(16px) - сбалансированное размытие (эффект стекла)
 *
 * ⚠️  Эти параметры были подобраны после множества итераций для достижения
 *    идеального баланса между прозрачностью, видимостью контента позади
 *    и стеклянным эффектом в стиле iOS.
 *
 * 📝 См. документацию: DESIGN_RULES.md - раздел "Стеклянное нижнее меню"
 * ================================================================================================
 */
.glassTabBar {
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 24px 32px;
  padding: 6px;
  border-radius: 40px;

  /* Сбалансированный стеклянный эффект - видно объекты сзади */
  background: rgba(253, 242, 248, 0.12);
  backdrop-filter: blur(16px) saturate(180%) brightness(105%);
  -webkit-backdrop-filter: blur(16px) saturate(180%) brightness(105%);

  /* Тень для глубины с контрастом */
  box-shadow:
    0 8px 32px rgba(0, 0, 0, 0.12),
    0 4px 16px rgba(0, 0, 0, 0.08),
    inset 0 1px 0 rgba(255, 255, 255, 0.8),
    inset 0 -1px 0 rgba(0, 0, 0, 0.08);

  /* Четкая граница для определения краев */
  border: 1px solid rgba(255, 255, 255, 0.8);

  /* Плавная анимация появления */
  animation: slideUp 0.4s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Кнопка таба */
.tabButton {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3px;
  padding: 6px 4px;
  min-height: 52px;
  background: transparent;
  border: none;
  border-radius: 16px;
  cursor: pointer;
  position: relative;
  transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);
  color: #666;
  -webkit-tap-highlight-color: transparent;
}

/* Иконка таба */
.tabIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative; /* Для абсолютного позиционирования badge */
  transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

/* Лейбл таба */
.tabLabel {
  font-size: 11px;
  font-weight: 500;
  line-height: 1.2;
  text-align: center;
  transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);
  position: relative;
  display: inline-flex;
  align-items: center;
  gap: 4px;
}

/* Бейдж с количеством */
.badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 18px;
  height: 18px;
  padding: 0 4px;
  font-size: 10px;
  font-weight: 600;
  color: white;
  background: linear-gradient(135deg, #FF6B9D 0%, #C239B3 100%);
  border-radius: 9px;
  box-shadow: 0 2px 4px rgba(194, 57, 179, 0.3);
}

/* Кружок с количеством циклов (вместо иконки) */
.cycleCountCircle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  border: 2px solid #666;
  border-radius: 50%;
  transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

.tabButton.active .cycleCountCircle {
  border-color: #FF6B9D;
}

.cycleCountNumber {
  font-size: 13px;
  font-weight: 600;
  color: inherit;
}

/* Квадратик с количеством дней до цикла (для вкладки Календарь) */
.calendarDaysSquare {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  border-left: 2px solid #666;
  border-right: 2px solid #666;
  border-bottom: 2px solid #666;
  border-top: 6px solid #666; /* Верхняя граница в 6 раз толще */
  border-radius: 4px;
  transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

.tabButton.active .calendarDaysSquare {
  border-left-color: #FF6B9D;
  border-right-color: #FF6B9D;
  border-bottom-color: #FF6B9D;
  border-top-color: #FF6B9D;
}

.calendarDaysNumber {
  font-size: 15px;
  font-weight: 700;
  color: inherit;
}

/* Notification badge для "Узнай себя" */
.notificationBadge {
  position: absolute;
  top: -4px;
  right: -6px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 20px;
  height: 20px;
  padding: 0 5px;
  font-size: 11px;
  font-weight: 700;
  color: white;
  background: linear-gradient(135deg, #FF6B9D 0%, #C239B3 100%);
  border-radius: 10px;
  border: 2px solid rgba(253, 242, 248, 0.95);
  box-shadow: 0 2px 8px rgba(194, 57, 179, 0.5);
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.8;
    transform: scale(1.1);
  }
}

/* Состояние нажатия */
.tabButton:active {
  transform: scale(0.95);
}

/* Активный таб */
.tabButton.active {
  color: #FF6B9D;
}

.tabButton.active .tabIcon {
  transform: scale(1.1);
}

.tabButton.active .tabLabel {
  font-weight: 600;
}

/* Эффект при hover (для десктопа) */
@media (hover: hover) {
  .tabButton:hover {
    background: rgba(255, 107, 157, 0.08);
  }

  .tabButton.active:hover {
    background: rgba(255, 107, 157, 0.12);
  }
}

/* Адаптация для темной темы */
@media (prefers-color-scheme: dark) {
  .glassTabBarGradient {
    background: linear-gradient(
      to top,
      rgba(253, 242, 248, 0.95) 0%,
      rgba(248, 237, 253, 0.85) 30%,
      rgba(253, 242, 248, 0.4) 60%,
      rgba(253, 242, 248, 0) 100%
    );
  }

  .glassTabBar {
    background: rgba(253, 242, 248, 0.12);
    backdrop-filter: blur(16px) saturate(180%) brightness(105%);
    -webkit-backdrop-filter: blur(16px) saturate(180%) brightness(105%);
    box-shadow:
      0 8px 32px rgba(0, 0, 0, 0.12),
      0 4px 16px rgba(0, 0, 0, 0.08),
      inset 0 1px 0 rgba(255, 255, 255, 0.8),
      inset 0 -1px 0 rgba(0, 0, 0, 0.08);
    border: 1px solid rgba(255, 255, 255, 0.8);
  }

  .tabButton {
    color: #666;
  }

  .tabButton.active {
    color: #FF6B9D;
  }

  @media (hover: hover) {
    .tabButton:hover {
      background: rgba(255, 107, 157, 0.08);
    }
  }
}

/* Адаптация для маленьких экранов */
@media (max-width: 360px) {
  .tabButton {
    min-height: 56px;
    padding: 6px 2px;
  }

  .tabIcon svg {
    width: 20px;
    height: 20px;
  }

  .tabLabel {
    font-size: 10px;
  }
}

/* Адаптация для больших экранов */
@media (min-width: 768px) {
  .glassTabBarContainer {
    max-width: 600px;
    left: 50%;
    transform: translateX(-50%);
  }
}
